.content {
  width: 100%;
  margin: 0 auto;
  font-size: 14px;
  line-height: 160%;
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 600;
    line-height: 180%;
    padding: 16px 0;
  }
  h1 {
    font-size: 0.18rem;
  }
  h2 {
    font-size: 0.16rem;
  }
  h3 {
    font-size: 0.14rem;
  }
  h4 {
    font-size: 0.12rem;
  }
  h5 {
    font-size: 0.1rem;
  }
  h6 {
    font-size: 0.08rem;
  }
  pre {
    border-radius: 10px;
    padding: 20px;
    // font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New,
    //   monospace;
    font-weight: 400;
    margin-bottom: 12px;
  }
  p {
    line-height: 160%;
    padding: 10px 0;
    font-weight: 400;
  }
  img {
    display: block;
    width: 80%;
    height: auto;
    margin: 0 auto;
    cursor: pointer;
  }
  table,
  td,
  th {
    border: 1px solid #dfdfdd;
  }
  td,
  th {
    padding: 10px;
  }
  a {
    color: #42b983;
  }
}
